import { React } from "react";
import "./Projects.css";
import Project from "../project/Project";
import { FirestoreCollection } from "@react-firebase/firestore";

export default function Projects() {
   return (
      <FirestoreCollection path="/projects/">
         {(d) => {
            return d.isLoading ? (
               <div className="main-container">
                  <div
                     style={{
                        display: "grid",
                        gridTemplateColumns: "repeat(10,10fr)",
                        gap: "10px",
                        gridAutoRows: "100px",
                     }}
                  >
                     <p
                        style={{
                           textAlign: "center",
                        }}
                     >
                        Loading
                     </p>
                  </div>
               </div>
            ) : (
               <div className="main-container">
                  <div
                     style={{
                        display: "grid",
                        width: "95%",
                        marginTop: "10px",
                        marginBottom: "10px",
                        gridTemplateColumns:
                           "repeat(" +
                           Math.ceil(Math.sqrt(d.value.length)) +
                           ",10fr)",
                        gap: "40px",
                        gridAutoRows: "100px",
                        gridTemplateRows:
                           "repeat(" +
                           Math.round(Math.sqrt(d.value.length)) +
                           ",10fr)",
                     }}
                  >
                     {d.value.map((projectDocument, index) => {
                        return (
                           <Project
                              document={projectDocument}
                              key={d.ids[index]}
                           ></Project>
                        );
                     })}
                     {/* <Project documents={d.value}></Project> */}
                  </div>
               </div>
            );
         }}
      </FirestoreCollection>
   );
}
